import React, { Component } from 'react';
import { FormControl, TextField, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Template.css';

class ApiUrl extends Component {
  state = {
    name: this.props.apiUrl.name,
    url: this.props.apiUrl.url
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      name: nextProps.apiUrl.name,
      url: nextProps.apiUrl.url
    });
  }

  deleteApiUrl = id => {
    this.props.deleteApiUrl(id);
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.props.handleApiUrlChange(
      this.props.apiUrl.id,
      name,
      event.target.value
    );
  };

  render() {
    const { classes, apiUrl } = this.props;
    const { name, url } = this.state;
    return (
      <FormControl className={classes.gridLayout}>
        <TextField
          id="name"
          label="name"
          variant="outlined"
          value={name}
          onChange={this.handleChange('name')}
          style={{ gridColumn: '1/4' }}
        />
        <TextField
          id="url"
          label="url"
          variant="outlined"
          value={url}
          onChange={this.handleChange('url')}
          style={{ gridColumn: '4/12' }}
        />
        <IconButton
          onClick={() => this.deleteApiUrl(apiUrl.id)}
          className={classes.iconButton}
          style={{ gridColumn: '12/13' }}
        >
          <DeleteIcon className="danger-btn" />
        </IconButton>
      </FormControl>
    );
  }
}
export default withStyles(styles)(ApiUrl);
