import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  Select,
  MenuItem,
  InputBase,
  Card,
  Box
} from '@material-ui/core';
import { Notification } from 'element-react';
import Loader from 'react-loader-spinner';
import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';
import styles from '../../styles/OrganizationView.css';
import TemplateAPI from '../../api/TemplateAPI';
import { UserContext } from '../../contexts/UserContext';
import Template from '../organization/Template';
import Configuration from '../../helpers/configuration';

const BootstrapInput = withStyles(theme => ({
  root: {
    height: '42px',
    marginTop: theme.spacing(3),
    overflowX: 'auto',
    margin: '20px auto',
    display: 'flex'
  },
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    width: '400px',
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    table: {
      minWidth: 700
    }
  }
}))(InputBase);

class OrganizationView extends Component {
  static contextType = UserContext;

  state = {
    organization: this.context.ownOrganizations[0],
    templatesList: []
  };

  componentDidMount() {
    this.updateRefCard();
  }

  getCurrentTemplate() {
    const { organization } = this.state;
    if (organization && organization.id) {
      TemplateAPI.getTemplate(organization.id)
        .then(result => {
          this.setState({ templatesList: [result] });
        })
        .catch(() => {});
    }
  }

  updateRefCard = () => {
    this.getCurrentTemplate();
  };

  selectOrganization = event => {
    this.setState({ organization: event.target.value }, () => {
      this.updateRefCard();
    });
  };

  updateTemplate = (idOrganization, templateData) => {
    TemplateAPI.updateTemplate(idOrganization, templateData)
      .then(() => {
        Notification.success({ title: 'Success', message: 'Changes saved' });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'There was a problem saving your changes'
        });
      });
  };

  render() {
    const { classes } = this.props;
    const {
      organization,
      templatesList
    } = this.state;
    const organizations = this.context.ownOrganizations;

    return (
      <div className={classes.bodyContainer} style={{ display: 'flex' }}>
        <Card className={classes.corpus}>
          <div className={classes.header}>
            <div className={classes.title}>
              {`${organization.name} guidelines`}
              <span className={classes.line} />
            </div>
            { !Configuration.GRAVITEE_ENABLED && <Select
                className={classes.organization}
                data-cy="organization-name-select"
                value={organization}
                onChange={this.selectOrganization}
                input={
                  <BootstrapInput
                    name="nameSelected"
                    id="age-customized-select"
                  />
                }
              >
                {organizations.map(orga => {
                  return (
                    <MenuItem key={orga.id} value={orga}>
                      {orga.name}
                    </MenuItem>
                  );
                })}
              </Select>
            }
          </div>

          {
            !Configuration.GRAVITEE_ENABLED && <div className={classes.infoBox}>
              <Box component="span" className={classes.boxContent} >
                Access to your organization portal:&nbsp;
                <a href={organization.portal} rel="noopener noreferrer" target="_blank">{organization.portal}</a>
              </Box>
            </div>
          }

          {(templatesList.length > 0 && (
            <Template
              selectedOrganization={organization}
              templatesList={templatesList}
              updateRefCard={this.updateRefCard}
              updateTemplate={this.updateTemplate}
            />
          )) || (
            <div className={classes.loaderContainer}>
              <Loader
                className={classes.spinner}
                type="TailSpin"
                color="#324157"
                height={50}
                width={50}
                timeout={60000}
              />
            </div>
          )}
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(OrganizationView);
