import React, { Component } from 'react';
import { FormControl, TextField, IconButton } from '@material-ui/core';
import TestIcon from '@material-ui/icons/Cast';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Template.css';

class GitRepo extends Component {
  state = {
    name: this.props.gitRepo.name,
    url: this.props.gitRepo.url,
    username: this.props.gitRepo.username,
    password: this.props.gitRepo.password
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      name: nextProps.gitRepo.name,
      url: nextProps.gitRepo.url,
      username: nextProps.gitRepo.username,
      password: nextProps.gitRepo.password
    });
  }

  deleteGitRepo = id => {
    this.props.deleteGitRepo(id);
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.props.handleGitRepoChange(
      this.props.gitRepo.id,
      name,
      event.target.value
    );
  };

  render() {
    const { classes, gitRepo } = this.props;
    const { name, url, username, password } = this.state;
    return (
      <FormControl className={classes.gridLayout}>
        <TextField
          id="name"
          label="name"
          variant="outlined"
          value={name}
          onChange={this.handleChange('name')}
          style={{ gridColumn: '1/3' }}
        />
        <TextField
          id="url"
          label="url"
          variant="outlined"
          value={url}
          onChange={this.handleChange('url')}
          style={{ gridColumn: '3/7' }}
        />
        <TextField
          id="username"
          label="username"
          variant="outlined"
          value={username}
          onChange={this.handleChange('username')}
          style={{ gridColumn: '7/9' }}
        />
        <TextField
          id="password"
          label="password"
          type="password"
          variant="outlined"
          value={password}
          onChange={this.handleChange('password')}
          style={{ gridColumn: '9/11' }}
        />
        <IconButton
          onClick={() => this.props.testGitRepo(gitRepo.id)}
          className={classes.iconButton}
          style={{ gridColumn: '11/12' }}
        >
          <TestIcon />
        </IconButton>
        <IconButton
          onClick={() => this.deleteGitRepo(gitRepo.id)}
          className={classes.iconButton}
          style={{ gridColumn: '12/13' }}
        >
          <DeleteIcon className="danger-btn" />
        </IconButton>
      </FormControl>
    );
  }
}
export default withStyles(styles)(GitRepo);
