import React, { Component } from 'react';
import {
  FormControl,
  TextField,
  IconButton,
  Select,
  FormLabel,
  FormGroup,
  FormControlLabel,
  Checkbox,
  InputLabel,
  MenuItem
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Template.css';

class Security extends Component {
  state = {
    type: this.props.security.type,
    name: this.props.security.name,
    keyIn: this.props.security.keyIn,
    scheme: this.props.security.scheme,
    flows: this.props.security.flows,
    authorizationUrl: this.props.security.authorizationUrl,
    tokenUrl: this.props.security.tokenUrl,
    refreshUrl: this.props.security.refreshUrl,
    openIdConnectUrl: this.props.security.openIdConnectUrl,
    description: this.props.security.description
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      type: nextProps.security.type,
      name: nextProps.security.name,
      keyIn: nextProps.security.keyIn,
      scheme: nextProps.security.scheme,
      flows: nextProps.security.flows,
      authorizationUrl: nextProps.security.authorizationUrl,
      tokenUrl: nextProps.security.tokenUrl,
      refreshUrl: nextProps.security.refreshUrl,
      openIdConnectUrl: nextProps.security.openIdConnectUrl,
      description: nextProps.security.description
    });
  }

  deleteSecurity = id => {
    this.props.deleteSecurity(id);
  };

  handleChange = name => event => {
    if (name === 'flows') {
      this.setState({
        flows: {
          ...this.state.flows,
          [event.target.name]: event.target.checked
        }
      });
      this.props.handleSecurityChange(this.props.security.id, name, {
        ...this.state.flows,
        [event.target.name]: event.target.checked
      });
    } else {
      this.setState({
        [name]: event.target.value
      });
      this.props.handleSecurityChange(
        this.props.security.id,
        name,
        event.target.value
      );
    }
  };

  render() {
    const { classes, security } = this.props;
    const {
      type,
      name,
      keyIn,
      scheme,
      flows,
      authorizationUrl,
      tokenUrl,
      refreshUrl,
      openIdConnectUrl,
      description
    } = this.state;
    return (
      <FormControl className={classes.gridLayout}>
        {type === 'apiKey' && (
          <>
            <FormControl variant="outlined" style={{ gridColumn: '1/3' }}>
              <InputLabel id="type-label">type</InputLabel>
              <Select
                id="type"
                label="type"
                labelId="type-label"
                value={type}
                onChange={this.handleChange('type')}
              >
                <MenuItem value="apiKey">API Key</MenuItem>
                <MenuItem value="http">Basic HTTP</MenuItem>
                <MenuItem value="oauth2">Oauth 2</MenuItem>
                <MenuItem value="openIdConnect">Open ID</MenuItem>
              </Select>
            </FormControl>
            <TextField
              id="name"
              label="name"
              variant="outlined"
              value={name}
              onChange={this.handleChange('name')}
              style={{ gridColumn: '3/5' }}
            />
            <FormControl variant="outlined" style={{ gridColumn: '5/7 ' }}>
              <InputLabel id="keyInLabel">in</InputLabel>
              <Select
                id="keyIn"
                label="in"
                labelId="keyInLabel"
                value={keyIn}
                onChange={this.handleChange('keyIn')}
                style={{ gridColumn: '5/7' }}
              >
                <MenuItem value="header">Header</MenuItem>
                <MenuItem value="query">Query</MenuItem>
                <MenuItem value="cookie">Cookie</MenuItem>
              </Select>
            </FormControl>
            <TextField
              id="description"
              label="description"
              variant="outlined"
              value={description}
              onChange={this.handleChange('description')}
              style={{ gridColumn: '7/12' }}
            />
            <IconButton
              onClick={() => this.deleteSecurity(security.id)}
              className={classes.iconButton}
              style={{ margin: 'auto', gridColumn: '12/13' }}
            >
              <DeleteIcon className="danger-btn" />
            </IconButton>
          </>
        )}
        {type === 'http' && (
          <>
            <FormControl variant="outlined" style={{ gridColumn: '1/3' }}>
              <InputLabel id="type-label">type</InputLabel>
              <Select
                id="type"
                label="type"
                labelId="type-label"
                value={type}
                onChange={this.handleChange('type')}
              >
                <MenuItem value="apiKey">API Key</MenuItem>
                <MenuItem value="http">Basic HTTP</MenuItem>
                <MenuItem value="oauth2">Oauth 2</MenuItem>
                <MenuItem value="openIdConnect">Open ID</MenuItem>
              </Select>
            </FormControl>
            <FormControl variant="outlined" style={{ gridColumn: '3/5 ' }}>
              <InputLabel id="scheme-label">scheme</InputLabel>
              <Select
                id="scheme"
                label="scheme"
                labelId="scheme-label"
                value={scheme}
                onChange={this.handleChange('scheme')}
              >
                <MenuItem value="basic">Basic</MenuItem>
                <MenuItem value="bearer">Bearer</MenuItem>
              </Select>
            </FormControl>
            <TextField
              id="description"
              label="description"
              variant="outlined"
              value={description}
              onChange={this.handleChange('description')}
              style={{ gridColumn: '5/12' }}
            />
            <IconButton
              onClick={() => this.deleteSecurity(security.id)}
              className={classes.iconButton}
              style={{ margin: 'auto', gridColumn: '12/13' }}
            >
              <DeleteIcon className="danger-btn" />
            </IconButton>
          </>
        )}
        {type === 'oauth2' && (
          <>
            <FormControl variant="outlined" style={{ gridColumn: '1/3' }}>
              <InputLabel id="type-label">type</InputLabel>
              <Select
                id="type"
                label="type"
                labelId="type-label"
                value={type}
                onChange={this.handleChange('type')}
              >
                <MenuItem value="apiKey">API Key</MenuItem>
                <MenuItem value="http">Basic HTTP</MenuItem>
                <MenuItem value="oauth2">Oauth 2</MenuItem>
                <MenuItem value="openIdConnect">Open ID</MenuItem>
              </Select>
            </FormControl>
            <FormControl component="fieldset" style={{ gridColumn: '3/5' }}>
              <FormLabel component="legend" style={{ fontSize: '12px' }}>
                Flows
              </FormLabel>
              <FormGroup>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={flows.authorizationCode}
                      onChange={this.handleChange('flows')}
                      name="authorizationCode"
                    />
                  }
                  label="Authorization code"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={flows.implicit}
                      onChange={this.handleChange('flows')}
                      name="implicit"
                    />
                  }
                  label="Implicit"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={flows.password}
                      onChange={this.handleChange('flows')}
                      name="password"
                    />
                  }
                  label="Pasword"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={flows.clientCredentials}
                      onChange={this.handleChange('flows')}
                      name="clientCredentials"
                    />
                  }
                  label="Client credentials"
                />
              </FormGroup>
            </FormControl>
            <FormControl component="fieldset" style={{ gridColumn: '5/9' }}>
              <FormLabel component="legend" style={{ fontSize: '12px' }}>
                Urls
              </FormLabel>
              <FormGroup>
                <TextField
                  id="authorizationUrl"
                  label="authorizationUrl"
                  variant="outlined"
                  value={authorizationUrl}
                  style={{ margin: '10px 0' }}
                  onChange={this.handleChange('authorizationUrl')}
                />
                <TextField
                  id="tokenUrl"
                  label="tokenUrl"
                  variant="outlined"
                  value={tokenUrl}
                  style={{ margin: '10px 0' }}
                  onChange={this.handleChange('tokenUrl')}
                />
                <TextField
                  id="refreshUrl"
                  label="refreshUrl"
                  variant="outlined"
                  value={refreshUrl}
                  style={{ margin: '10px 0' }}
                  onChange={this.handleChange('refreshUrl')}
                />
              </FormGroup>
            </FormControl>
            <TextField
              id="description"
              label="description"
              multiline
              rows={11}
              variant="outlined"
              value={description}
              onChange={this.handleChange('description')}
              style={{ gridColumn: '9/12' }}
            />
            <IconButton
              onClick={() => this.deleteSecurity(security.id)}
              className={classes.iconButton}
              style={{ margin: 'auto', gridColumn: '12/13' }}
            >
              <DeleteIcon className="danger-btn" />
            </IconButton>
          </>
        )}
        {type === 'openIdConnect' && (
          <>
            <FormControl variant="outlined" style={{ gridColumn: '1/3' }}>
              <InputLabel id="type-label">type</InputLabel>
              <Select
                id="type"
                label="type"
                labelId="type-label"
                value={type}
                onChange={this.handleChange('type')}
              >
                <MenuItem value="apiKey">API Key</MenuItem>
                <MenuItem value="http">Basic HTTP</MenuItem>
                <MenuItem value="oauth2">Oauth 2</MenuItem>
                <MenuItem value="openIdConnect">Open ID</MenuItem>
              </Select>
            </FormControl>
            <TextField
              id="openIdConnectUrl"
              label="openIdConnectUrl"
              variant="outlined"
              value={openIdConnectUrl}
              onChange={this.handleChange('openIdConnectUrl')}
              style={{ gridColumn: '3/7' }}
            />
            <TextField
              id="description"
              label="description"
              variant="outlined"
              value={description}
              onChange={this.handleChange('description')}
              style={{ gridColumn: '7/12' }}
            />
            <IconButton
              onClick={() => this.deleteSecurity(security.id)}
              className={classes.iconButton}
              style={{ margin: 'auto', gridColumn: '12/13' }}
            >
              <DeleteIcon className="danger-btn" />
            </IconButton>
          </>
        )}
      </FormControl>
    );
  }
}
export default withStyles(styles)(Security);
