import React, { Component } from 'react';
import { FormControl, TextField, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Template.css';

class CustomResponse extends Component {
  state = {
    statusCode: this.props.customResponse.statusCode,
    description: this.props.customResponse.description,
    responseMessage: this.props.customResponse.responseMessage
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      statusCode: nextProps.customResponse.statusCode,
      description: nextProps.customResponse.description,
      responseMessage: nextProps.customResponse.responseMessage
    });
  }

  deleteCustomResponse = id => {
    this.props.deleteCustomResponse(id);
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.props.handleCustomResponseChange(
      this.props.customResponse.id,
      name,
      event.target.value
    );
  };

  render() {
    const { classes, customResponse } = this.props;
    const { statusCode, description, responseMessage } = this.state;
    return (
      <FormControl className={classes.gridLayout}>
        <TextField
          id="statusCode"
          label="status code"
          variant="outlined"
          value={statusCode}
          onChange={this.handleChange('statusCode')}
          style={{ gridColumn: '1/3' }}
        />
        <TextField
          id="description"
          label="description"
          variant="outlined"
          value={description}
          onChange={this.handleChange('description')}
          style={{ gridColumn: '3/9' }}
        />
        <TextField
          id="responseMessage"
          label="response message"
          variant="outlined"
          value={responseMessage}
          onChange={this.handleChange('responseMessage')}
          style={{ gridColumn: '9/12' }}
        />
        <IconButton
          onClick={() => this.deleteCustomResponse(customResponse.id)}
          className={classes.iconButton}
          style={{ gridColumn: '12/13' }}
        >
          <DeleteIcon className="danger-btn" />
        </IconButton>
      </FormControl>
    );
  }
}
export default withStyles(styles)(CustomResponse);
