const styles = theme => ({
  templateForm: {
    textAlign: 'left'
  },
  urlInput: {
    margin: 'auto'
  },
  globalDescription: {
    width: 'fit-content',
    padding: theme.spacing(2)
  },
  addStatusCode: {
    width: 'fit-content',
    margin: 'auto'
  },
  button: {
    margin: '10px'
  },
  buttonAdd: {
    marginTop: '16px',
    width: '55px',
    height: '56px',
    minWidth: '0px'
  },
  selectEmpty: {
    marginTop: '16px'
  },
  textField: {
    marginRight: '20px'
  },
  textFieldStatusCode: {
    width: '15%'
  },
  textFielCustom: {
    width: '35%'
  },
  caseRadioButtonContainer: {
    margin: theme.spacing(0)
  },
  radioButtonContainer: {
    height: 'fit-content'
  },
  radioGroup: {
    display: 'inline'
  },
  radioButton: {
    padding: '10px'
  },
  swaggerVersion: {
    margin: theme.spacing(2)
  },
  customOption: {
    margin: theme.spacing(2)
  },
  responseOption: {
    margin: theme.spacing(2)
  },
  descriptionTemplate: {
    padding: theme.spacing(2)
  },
  textFieldMetaDescription: {
    margin: 0,
    width: '-webkit-fill-available'
  },
  textFieldApiUrl: {
    width: '-webkit-fill-available'
  },
  metaDescription: {
    marginBottom: '30px',
    width: '100%'
  },
  appBar: {
    backgroundColor: '#324157',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    borderRadius: '5px',
    '&$selected': {
      fontWeight: theme.typography.fontWeightMedium
    }
  },
  subtitle: {
    marginTop: '10px'
  },
  body: {
    margin: '0 2.5rem'
  },
  templateButtonDiv: {
    margin: '2rem 2.5rem 2.5rem 0',
    textAlign: 'right'
  },
  templateButton: {
    marginLeft: '1rem',
    width: '150px'
  },
  addButton: {
    display: 'block',
    float: 'right',
    marginBottom: '1.5rem',
  },
  iconButton: {
    height: '3rem',
    width: '3rem',
    alignSelf: 'center',
    justifySelf: 'center'
  },
  gridLayout: {
    display: 'grid',
    gridGap: '0.88rem',
    gridTemplateColumns: 'repeat(12, 4rem)',
    marginBottom: '1.5rem'
  }
});

export default styles;
