import BaseAPI from './BaseAPI';

export default class SwaggerAPI extends BaseAPI {
  static updateTemplate(organizationId, templateData) {
    return this.execute('put', `/organizations/${organizationId}/template`, {
      data: templateData
    });
  }

  static getTemplate(organizationId) {
    return this.execute('get', `/organizations/${organizationId}/template`);
  }
}
