import React, { Component } from 'react';
import {
  FormControl,
  TextField,
  FormControlLabel,
  Checkbox,
  IconButton,
  Tooltip
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import styles from '../../styles/Template.css';

class CustomHeader extends Component {
  state = {
    name: this.props.customHeader.name,
    value: this.props.customHeader.value,
    description: this.props.customHeader.description,
    isRequestHeader: this.props.customHeader.isRequestHeader
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.setState({
      name: nextProps.customHeader.name,
      value: nextProps.customHeader.value,
      description: nextProps.customHeader.description,
      isRequestHeader: nextProps.customHeader.isRequestHeader
    });
  }

  deleteCustomHeader = id => {
    this.props.deleteCustomHeader(id);
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.props.handleCustomHeaderChange(
      this.props.customHeader.id,
      name,
      event.target.value
    );
  };

  handleChangeIsRequest = event => {
    this.setState({ isRequestHeader: event.target.checked });
    this.props.handleCustomHeaderChange(
      this.props.customHeader.id,
      'isRequestHeader',
      event.target.checked
    );
  };

  render() {
    const { classes, customHeader } = this.props;
    const { name, value, description, isRequestHeader } = this.state;
    return (
      <FormControl className={classes.gridLayout}>
        <TextField
          id="name"
          label="name"
          variant="outlined"
          value={name}
          onChange={this.handleChange('name')}
          style={{ gridColumn: '1/3' }}
        />
        <TextField
          id="value"
          label="value"
          variant="outlined"
          value={value}
          onChange={this.handleChange('value')}
          style={{ gridColumn: '3/5' }}
        />
        <TextField
          id="description"
          label="description"
          variant="outlined"
          value={description}
          onChange={this.handleChange('description')}
          style={{ gridColumn: '5/11' }}
        />
        <Tooltip title="Check if it is a request header" placement="bottom">
          <FormControlLabel
            control={
              <Checkbox
                checked={isRequestHeader}
                color="default"
                onChange={this.handleChangeIsRequest}
              />
            }
            label="Request"
            labelPlacement="top"
            style={{ gridColumn: '11/12' }}
          />
        </Tooltip>
        <IconButton
          onClick={() => this.deleteCustomHeader(customHeader.id)}
          className={classes.iconButton}
          style={{ gridColumn: '12/13' }}
        >
          <DeleteIcon className="danger-btn" />
        </IconButton>
      </FormControl>
    );
  }
}
export default withStyles(styles)(CustomHeader);
