/* eslint-disable react/no-array-index-key */
/* eslint-disable no-param-reassign */
import React, { Component } from 'react';
import uniqid from 'uniqid';
import {
  AppBar,
  Typography,
  Box,
  FormControl,
  RadioGroup,
  Radio,
  FormControlLabel,
  Tabs,
  Tab,
  TextField,
  Button,
  Card,
  Tooltip
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { Notification } from 'element-react';
import styles from '../../styles/Template.css';
import GitReposAPI from '../../api/GitReposAPI';
import Security from './Security';
import ApiUrl from './ApiUrl';
import CustomHeader from './CustomHeader';
import CustomResponse from './CustomResponse';
import GitRepo from './GitRepo';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

function CloneProps(props) {
  const { children, ...other } = props;
  return children(other);
}

class Template extends Component {
  state = {
    value: 0,
    radioValue: this.props.templatesList[0].case,
    metaDescription: this.props.templatesList[0].metaDescription,
    swaggerVersion: this.props.templatesList[0].swaggerVersion,
    securitiesList: this.props.templatesList[0].security,
    apiUrlsList: this.props.templatesList[0].apiUrls,
    customHeadersList: this.props.templatesList[0].customHeaders,
    customResponsesList: this.props.templatesList[0].statusCodes,
    gitReposList: []
  };

  // eslint-disable-next-line camelcase
  UNSAFE_componentWillReceiveProps(nextProps) {
    this.getCurrentGitRepos();
    this.setState({
      value: 0,
      radioValue: nextProps.templatesList[0].case,
      metaDescription: nextProps.templatesList[0].metaDescription,
      swaggerVersion: nextProps.templatesList[0].swaggerVersion,
      securitiesList: nextProps.templatesList[0].security,
      apiUrlsList: nextProps.templatesList[0].apiUrls,
      customHeadersList: nextProps.templatesList[0].customHeaders,
      customResponsesList: nextProps.templatesList[0].statusCodes,
    });
  }

  formPreventDefault = e => {
    e.preventDefault();
  };

  handleTabChange = (event, value) => {
    this.setState({ value });
  };

  handleRadioButton = (event, newRadio) => {
    this.setState({ radioValue: newRadio });
  };

  handleSwaggerVersion = (event, swaggerVersion) => {
    this.setState({ swaggerVersion });
  };

  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };

  getCurrentGitRepos() {
    const organization = this.props.selectedOrganization;
    if (organization && organization.id) {
      GitReposAPI.getRepos(organization.id)
        .then(result => {
          this.setState({
            gitReposList: result.map(repo => {
              return {
                id: repo.id,
                name: repo.name,
                url: repo.url,
                username: repo.git_username,
                password: repo.git_password
              };
            })
          });
        })
        .catch(() => {});
    }
  }

  addGitRepo = repo => {
    GitReposAPI.addGitRepo(this.props.selectedOrganization.id, repo)
      .then(() => {
        this.getCurrentGitRepos();
      })
      .catch(() => {});
  };

  updateGitRepo = (gitId, repo) => {
    GitReposAPI.updateGit(gitId, {
      ...repo,
      git_username: repo.username,
      git_password: repo.password
    })
      .then(() => {
        this.getCurrentGitRepos();
      })
      .catch(() => {});
  };

  deleteGitRepo = gitId => {
    GitReposAPI.deleteGit(gitId)
      .then(() => {
        this.getCurrentGitRepos();
      })
      .catch(() => {});
  };

  testGitRepo = gitId => {
    GitReposAPI.testGit(gitId)
      .then(() => {
        Notification.success({
          title: 'Success',
          message: 'Test to commit to git was successful'
        });
      })
      .catch(() => {
        Notification.error({
          title: 'Error',
          message: 'Test to commit to git was unsuccessful'
        });
      });
  };

  addSecurity = security => {
    const { securitiesList } = this.state;
    if (securitiesList) {
      const joinedSecurity = securitiesList.concat(security);
      this.setState({ securitiesList: joinedSecurity });
    } else {
      this.setState(() => ({
        securitiesList: [security]
      }));
    }
  };

  addApiUrl = apiUrl => {
    const { apiUrlsList } = this.state;
    if (apiUrlsList) {
      const joinedApiUrl = apiUrlsList.concat(apiUrl);
      this.setState({ apiUrlsList: joinedApiUrl });
    } else {
      this.setState(() => ({
        apiUrlsList: [apiUrl]
      }));
    }
  };

  addCustomHeader = customHeader => {
    const { customHeadersList } = this.state;
    if (customHeadersList) {
      const joinedCustomHeader = customHeadersList.concat(customHeader);
      this.setState({ customHeadersList: joinedCustomHeader });
    } else {
      this.setState(() => ({
        customHeadersList: [customHeader]
      }));
    }
  };

  addCustomResponse = customResponse => {
    const { customResponsesList } = this.state;
    if (customResponsesList) {
      const joinedCustomResponse = customResponsesList.concat(customResponse);
      this.setState({ customResponsesList: joinedCustomResponse });
    } else {
      this.setState(() => ({
        customResponsesList: [customResponse]
      }));
    }
  };

  handleSecurityChange = (securityId, name, value) => {
    let { securitiesList } = this.state;
    securitiesList = securitiesList.map(security => {
      if (securityId === security.id) {
        security[name] = value;
      }
      return security;
    });
    this.setState({ securitiesList });
  };

  handleApiUrlChange = (apiUrlId, name, value) => {
    let { apiUrlsList } = this.state;
    apiUrlsList = apiUrlsList.map(apiUrl => {
      if (apiUrlId === apiUrl.id) {
        apiUrl[name] = value;
      }
      return apiUrl;
    });
    this.setState({ apiUrlsList });
  };

  handleCustomHeaderChange = (customHeaderId, name, value) => {
    let { customHeadersList } = this.state;
    customHeadersList = customHeadersList.map(customHeader => {
      if (customHeaderId === customHeader.id) {
        customHeader[name] = value;
      }
      return customHeader;
    });
    this.setState({ customHeadersList });
  };

  handleCustomResponseChange = (customResponseId, name, value) => {
    let { customResponsesList } = this.state;
    customResponsesList = customResponsesList.map(customResponse => {
      if (customResponseId === customResponse.id) {
        customResponse[name] = value;
      }
      return customResponse;
    });
    this.setState({ customResponsesList });
  };

  handleGitRepoChange = (gitRepoId, name, value) => {
    const { gitReposList } = this.state;
    const updatedGitRepoList = gitReposList.map(gitRepo => {
      if (gitRepoId === gitRepo.id) {
        gitRepo[name] = value;
        this.updateGitRepo(gitRepoId, gitRepo);
      }
      return gitRepo;
    });
    this.setState({ gitReposList: updatedGitRepoList });
  };

  deleteSecurity = securityId => {
    this.setState(prevState => ({
      securitiesList: prevState.securitiesList.filter(
        security => security.id !== securityId
      )
    }));
  };

  deleteApiUrl = apiUrlId => {
    this.setState(prevState => ({
      apiUrlsList: prevState.apiUrlsList.filter(
        apiUrl => apiUrl.id !== apiUrlId
      )
    }));
  };

  deleteCustomHeader = customHeaderId => {
    this.setState(prevState => ({
      customHeadersList: prevState.customHeadersList.filter(
        customHeader => customHeader.id !== customHeaderId
      )
    }));
  };

  deleteCustomResponse = customResponseId => {
    this.setState(prevState => ({
      customResponsesList: prevState.customResponsesList.filter(
        customResponse => customResponse.id !== customResponseId
      )
    }));
  };

  handleClearFormBtn = () => {
    this.props.updateRefCard();
  };

  updateTemplate = async () => {
    const { selectedOrganization, updateTemplate } = this.props;
    const {
      metaDescription,
      swaggerVersion,
      securitiesList,
      apiUrlsList,
      customHeadersList,
      customResponsesList,
      radioValue
    } = this.state;
    const newTemplateData = {
      case: radioValue,
      metaDescription: metaDescription || '',
      swaggerVersion,
      security: securitiesList,
      apiUrls: apiUrlsList,
      customHeaders: customHeadersList,
      statusCodes: customResponsesList,
      queryParams: []
    };
    await updateTemplate(selectedOrganization.id, newTemplateData);
  };

  render() {
    const { classes } = this.props;
    const {
      value,
      radioValue,
      metaDescription,
      swaggerVersion,
      securitiesList,
      apiUrlsList,
      customHeadersList,
      customResponsesList,
      gitReposList
    } = this.state;
    const invalidOrganization =
      this.props.selectedOrganization.name === 'Personal';
    return (
      <form onSubmit={this.formPreventDefault} className={classes.templateForm}>
        <Card className={classes.body}>
          <AppBar position="static" className={classes.appBar}>
            <Tabs
              indicatorColor="primary"
              value={value}
              onChange={this.handleTabChange}
              aria-label="simple tabs example"
            >
              <Tab label="Global settings" {...a11yProps(0)} />
              <Tab label="Security" {...a11yProps(1)} />
              <Tab label="Custom Headers" {...a11yProps(2)} />
              <Tab label="Custom Responses" {...a11yProps(3)} />
              <CloneProps>
                {tabProps => (
                  <Tooltip
                    title="Repositories are not available in free mode"
                    placement="top"
                    disableFocusListener={!invalidOrganization}
                    disableHoverListener={!invalidOrganization}
                    disableTouchListener={!invalidOrganization}
                  >
                    <div>
                      <Tab
                        label="Repository"
                        {...tabProps}
                        {...a11yProps(4)}
                        disabled={invalidOrganization}
                      />
                    </div>
                  </Tooltip>
                )}
              </CloneProps>
              <Tab label="URLs" {...a11yProps(5)} />
            </Tabs>
          </AppBar>
          <TabPanel value={value} index={0}>
            <TextField
              data-cy="meta-description"
              label="Global description of the API"
              className={classes.metaDescription}
              placeholder="Enter a meta description..."
              rows="4"
              variant="outlined"
              multiline
              value={metaDescription || ''}
              onChange={this.handleChange('metaDescription')}
            />
            <Typography className={classes.subtitle}>Case:</Typography>
            <FormControl
              component="fieldset"
              className={classes.caseRadioButtonContainer}
            >
              <RadioGroup
                aria-label=""
                value={radioValue}
                className={classes.radioGroup}
                onChange={this.handleRadioButton}
              >
                <FormControlLabel
                  value="CAMEL_CASE"
                  className={classes.radioButton}
                  control={<Radio />}
                  label="camelCase"
                />
                <FormControlLabel
                  value="SNAKE_CASE"
                  className={classes.radioButton}
                  control={<Radio />}
                  label="snake_case"
                />
                <FormControlLabel
                  value="KEBAB_CASE"
                  className={classes.radioButton}
                  control={<Radio />}
                  label="kebab-case"
                />
              </RadioGroup>
            </FormControl>
            <Typography className={classes.subtitle}>
              Swagger version:
            </Typography>
            <FormControl
              component="fieldset"
              className={classes.caseRadioButtonContainer}
            >
              <RadioGroup
                aria-label=""
                value={swaggerVersion}
                className={classes.radioGroup}
                onChange={this.handleSwaggerVersion}
              >
                <FormControlLabel
                  value="2"
                  className={classes.radioButton}
                  control={<Radio />}
                  label="Swagger v2"
                />
                <FormControlLabel
                  value="3"
                  className={classes.radioButton}
                  control={<Radio />}
                  label="OpenAPI v3"
                />
              </RadioGroup>
            </FormControl>
          </TabPanel>
          <TabPanel value={value} index={1}>
            {securitiesList && (
              <>
                {securitiesList.map((security, key) => (
                  <Security
                    key={key}
                    security={security}
                    deleteSecurity={this.deleteSecurity}
                    handleSecurityChange={this.handleSecurityChange}
                  />
                ))}
              </>
            )}
            <Button
              variant="contained"
              color="primary"
              className={classes.addButton}
              onClick={() =>
                this.addSecurity({
                  id: uniqid(),
                  type: 'apiKey',
                  name: 'X-API-Key',
                  keyIn: 'header',
                  scheme: 'bearer',
                  flows: {
                    authorizationCode: false,
                    implicit: false,
                    password: false,
                    clientCredentials: false
                  },
                  authorizationUrl: 'https://example.com/auth',
                  tokenUrl: 'https://example.com/auth/token',
                  refreshUrl: 'https://example.com/auth/refresh',
                  openIdConnectUrl: 'https://example.com/auth',
                  description: 'description'
                })
              }
            >
              Add one security
            </Button>
          </TabPanel>
          <TabPanel value={value} index={2}>
            {customHeadersList && (
              <>
                {customHeadersList.map((customHeader, key) => (
                  <CustomHeader
                    key={key}
                    customHeader={customHeader}
                    deleteCustomHeader={this.deleteCustomHeader}
                    handleCustomHeaderChange={this.handleCustomHeaderChange}
                  />
                ))}
              </>
            )}
            <Button
              variant="contained"
              color="primary"
              className={classes.addButton}
              onClick={() =>
                this.addCustomHeader({
                  id: uniqid(),
                  name: 'customHeader',
                  value: 'value',
                  description: 'description',
                  isRequestHeader: false
                })
              }
            >
              Add one header
            </Button>
          </TabPanel>
          <TabPanel value={value} index={3}>
            {customResponsesList && (
              <>
                {customResponsesList.map((customResponse, key) => (
                  <CustomResponse
                    key={key}
                    customResponse={customResponse}
                    deleteCustomResponse={this.deleteCustomResponse}
                    handleCustomResponseChange={this.handleCustomResponseChange}
                  />
                ))}
              </>
            )}
            <Button
              variant="contained"
              color="primary"
              className={classes.addButton}
              onClick={() =>
                this.addCustomResponse({
                  id: uniqid(),
                  statusCode: '404',
                  description: 'description',
                  responseMessage: '{"message": "NOT_FOUND"}'
                })
              }
            >
              Add one response
            </Button>
          </TabPanel>
          <TabPanel value={value} index={4}>
            {gitReposList && (
              <>
                {gitReposList.map((gitRepo, key) => (
                  <GitRepo
                    key={key}
                    gitRepo={gitRepo}
                    testGitRepo={this.testGitRepo}
                    deleteGitRepo={this.deleteGitRepo}
                    handleGitRepoChange={this.handleGitRepoChange}
                  />
                ))}
              </>
            )}
            <Button
              variant="contained"
              color="primary"
              className={classes.addButton}
              onClick={() =>
                this.addGitRepo({
                  id: uniqid(),
                  name: 'api-dev',
                  url: 'https://api-dev.apizr.fr',
                  username: 'username',
                  password: 'password'
                })
              }
            >
              Add one Git repo
            </Button>
          </TabPanel>
          <TabPanel value={value} index={5}>
            {apiUrlsList && (
              <>
                {apiUrlsList.map((apiUrl, key) => (
                  <ApiUrl
                    key={key}
                    apiUrl={apiUrl}
                    deleteApiUrl={this.deleteApiUrl}
                    handleApiUrlChange={this.handleApiUrlChange}
                  />
                ))}
              </>
            )}
            <Button
              variant="contained"
              color="primary"
              className={classes.addButton}
              onClick={() =>
                this.addApiUrl({
                  id: uniqid(),
                  name: 'api-dev',
                  url: 'https://api-dev.apizr.fr'
                })
              }
            >
              Add one URL
            </Button>
          </TabPanel>
        </Card>
        <div className={classes.templateButtonDiv}>
          <Button
            variant="outlined"
            color="primary"
            className={classes.templateButton}
            onClick={this.handleClearFormBtn}
          >
            Discard
          </Button>
          <Button
            variant="contained"
            color="primary"
            className={classes.templateButton}
            onClick={this.updateTemplate}
          >
            Save
          </Button>
        </div>
      </form>
    );
  }
}

export default withStyles(styles)(Template);
